body {
  color: #999;
  background-color: #2b283d;
}

.timer__items {
  max-width: max-content;
  margin: 0 auto;
  font-size: 48px;
  display: flex;
}

.timer__box {
  width: 200px;
  height: 200px;
  border: 3px solid #646172;
  border-radius: 50%;
  position: relative;
}

.timer__box:not(:last-child) {
  margin-right: 10px;
}

.timer__item {
  min-width: 60px;
  text-align: center;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 15px;
  display: flex;
  position: relative;
}

.timer__item:before {
  content: attr(data-title);
  font-size: 14px;
  display: block;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
}

.button {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 50px;
  display: flex;
}

.button button {
  padding: 10px 20px;
}

